
'use client';

import { useEffect, useState } from 'react';
import type { User, Building } from '@/types';
import { getRawBuildings, fetchUserSessionForClient } from '@/app/actions';
import BuildingCard from '@/components/minecraft/BuildingCard';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { ServerCrash, Inbox, Loader2 } from 'lucide-react';
import { Skeleton } from '@/components/ui/skeleton';
import WorksFilterControls from '@/components/filters/WorksFilterControls';

export default function HomePage() {
  const [allWorks, setAllWorks] = useState<Building[]>([]);
  const [displayedWorks, setDisplayedWorks] = useState<Building[]>([]);
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      setLoading(true);
      setError(null);
      try {
        const [worksData, userData] = await Promise.all([
          getRawBuildings().catch(e => { console.error("Failed to fetch all works", e); return []; }),
          fetchUserSessionForClient().catch(e => { console.error("Failed to fetch user session", e); return null; })
        ]);
        setAllWorks(worksData);
        setDisplayedWorks(worksData); 
        setCurrentUser(userData);
      } catch (err) {
        console.error("Error fetching homepage data:", err);
        setError("无法加载作品数据，请稍后再试。");
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  if (error) {
    return (
      <div className="py-8 px-4"> {/* Adjusted padding for consistency with RootLayout */}
        <Alert variant="destructive" className="mt-6 max-w-2xl mx-auto">
          <ServerCrash className="h-5 w-5" />
          <AlertTitle>数据加载错误</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      </div>
    );
  }
  
  if (loading) {
    return (
      <div className="space-y-6">
        <h2 className="text-3xl font-bold text-foreground tracking-tight">作品画廊</h2>
        <Card className="mb-6 shadow-sm border-border/30 bg-card/50">
          <CardContent className="p-3 sm:p-4 space-y-3 md:space-y-0 md:flex md:flex-wrap md:items-end md:gap-3">
            <div className="flex-grow min-w-[180px] md:max-w-sm"><Skeleton className="h-6 w-24 mb-1.5" /><Skeleton className="h-9 w-full" /></div>
            <div className="min-w-[120px] sm:min-w-[140px]"><Skeleton className="h-6 w-16 mb-1.5" /><Skeleton className="h-9 w-full" /></div>
            <div className="min-w-[120px] sm:min-w-[140px]"><Skeleton className="h-6 w-16 mb-1.5" /><Skeleton className="h-9 w-full" /></div>
          </CardContent>
        </Card>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
          {[...Array(8)].map((_, i) => (
            <div key={i} className="bg-card border-border rounded-lg shadow-md flex flex-col overflow-hidden">
              <Skeleton className="w-full aspect-[16/9]" />
              <div className="p-4 flex flex-col flex-grow space-y-2">
                <Skeleton className="h-5 w-3/4" />
                <Skeleton className="h-3 w-1/2" />
                <Skeleton className="h-3 w-full" />
                <Skeleton className="h-3 w-2/3" />
                <div className="mt-auto flex justify-between items-center pt-2">
                    <div className="flex gap-3">
                        <Skeleton className="h-4 w-12" />
                        <Skeleton className="h-4 w-10" />
                    </div>
                    <Skeleton className="h-8 w-24" />
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }


  return (
    <div className="space-y-6">
      <h2 className="text-3xl font-bold text-foreground tracking-tight">
        作品画廊
      </h2>

      <WorksFilterControls
        initialWorks={allWorks}
        onFilteredWorks={setDisplayedWorks}
      />
      
      {displayedWorks.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
          {displayedWorks.map((building) => (
            <BuildingCard 
              key={building.id} 
              building={building} 
              currentUser={currentUser} 
            />
          ))}
        </div>
      ) : (
         <Alert className="border-primary/30 bg-primary/5 text-center py-10">
            <Inbox className="h-12 w-12 text-primary mx-auto mb-4" />
            <AlertTitle className="text-xl font-semibold text-primary/90 mb-2">
                {allWorks.length > 0 && searchTerm ? '没有匹配的作品' : '暂无作品'}
            </AlertTitle>
            <AlertDescription className="text-primary/80">
                {allWorks.length > 0 && searchTerm ? '请尝试调整搜索或筛选条件。' : '目前还没有任何作品提交。敬请期待！'}
            </AlertDescription>
        </Alert>
      )}
    </div>
  );
}

// Need to import Card and CardContent for the skeleton
import { Card, CardContent } from '@/components/ui/card';
